@media (max-width: 768px) {
    
    
.gallery-menu{
    height: auto;
    display: flex;
    flex-wrap: wrap;
    background-color: rgb(52, 53, 51);
    gap: 16px;
    padding: 2%;
    overflow-x: hidden;
    justify-content: center;
    align-items: center;
    margin: 0;
}
.gallery-content{
    border: #CEC5B6 solid 2px;
    background-color: rgb(52, 53, 51);
    gap: 5%;
    overflow: hidden;
    flex: 1 1 calc(45%);
    flex-direction: column;
    display: flex;
    width: 30%;
    padding: 2%;
    height: auto;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    position: relative;
    box-shadow: 6px 5px 33px 7px rgba(0, 0, 0, 0.43);
    transition: transform 0.2s ease;
    color: aliceblue;
    font-family: 'SecondFont';
    cursor: default;
}
.gallery-main{
    display: flex;
    width:100%;
    height: 100%;
    border-top: #CEC5B6 solid 2px;
    flex-direction: column;
   
}
.Type{
    display: flex;
    text-align: center;
    align-items: center;
    flex-direction: column;
    border-right: #CEC5B6 solid 2px;
    border-left: #CEC5B6 solid 2px;
    width:100%;
    height: 100%;
    border-bottom: #CEC5B6 solid 2px;
    padding: 2% 0;
}
.Type h1{
    font-size: clamp(1rem,1vw,3rem);
}
.Type:hover{
    background-color: rgb(0,0,0,0.43);
}
.gallery-picture{
    width:50vw;
    border: #CEC5B6 solid 2px;
    height:50vw ;
}


}